import React, { Component } from 'react';
import { Grid, Cell } from 'react-mdl';

class TentangSaya extends Component {
  render() {
    return(
      <div className="content-body">
        <Grid className="content-grid2">
        <Cell col={3}>
          <div className="content-list">
            <img
              src="http://bukulokomedia.com/lukman.png"
              alt="avatar"
               />
          <h4>Lukmanul Hakim</h4>
          <hr style={{borderTop: '3px solid #833fb2'}}/>
          <p>Alamat:</p>
          <p>Pesona Alam Hijau II B-4 Yogyakarta</p>
          <p>Phone: (0274) 5306310</p>
          <p>Email: bukulokomedia@gmail.com</p>
          <p>Web: http://www.bukulokomedia.com</p>
          </div>
        </Cell>

        <Cell className="resume-right-col" col={9}>
            <h3>Pendidikan</h3>
            <Grid>
              <Cell col={4}>
                <p>2000 - 2005</p>
              </Cell>
              <Cell col={8}>
                <h5 style={{marginTop:'0px'}}>S1 Universitas Gadjah Mada</h5>
              </Cell>
            </Grid>
            <Grid>
              <Cell col={4}>
                <p>2005 - 2008</p>
              </Cell>
              <Cell col={8}>
                <h5 style={{marginTop:'0px'}}>S2 Magister Teknologi Informasi</h5>
              </Cell>
            </Grid>
            <hr style={{borderTop: '3px solid #e22947'}} />
            <h3>Pengalaman</h3>
            <Grid>
              <Cell col={4}>
                <p>2009 - 2011</p>
              </Cell>
              <Cell col={8}>
                <h5 style={{marginTop:'0px'}}>Programmer PT. Vista Computama</h5>
              </Cell>
              <Cell col={4}>
                <p>2012 - 2015</p>
              </Cell>
              <Cell col={8}>
                <h5 style={{marginTop:'0px'}}>Instruktur PT. Inixindo Jogjakarta</h5>
              </Cell>
            </Grid>
        </Cell>
        </Grid>
      </div>
    )
  }

}

export default TentangSaya;



